.wrapper{
  width: 80%;
  margin: 50px auto;
  height: 100vh;
  position: relative;
  color: crimson;
}

.title{
  font-size: 48px;
  text-align: center;
}

.canvas {
  overflow: hidden;
  display: block;
  background-color: #000000;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.currentScore {
  left: 20px;
}
.topScore {
  right: 20px;
}
.score {
  display: block;
  position: absolute;
  top: 15px;
  z-index: 1;
  font-size: 20px;
}
.controls {
  display: block;
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
  font-size: 11px;
  text-align: center;
  line-height: 1.6;
}
.endgame{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 16px;
  z-index: 1;
  text-align: center;
}
.button{
  border: 4px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 20px;
  margin: 10px;
  font-family: 'PT Mono', serif;
  cursor: pointer;
}
.button:hover{
  background-color: #ffffff;
  color: #000000;
}

@media (max-width: 768px) {
  .score{
    font-size: 12px;
  }

  .controls{
    display: none;
  }
}
